body {
    background-color: #590D17;
    background-image: url('http://paper-backgrounds.com/textureimages/2014/03/red-grunge-concrete-texture-hd.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    font-family: 'Sawarabi Mincho', sans-serif;
    font-size: 1.1em;
    color: #FFEEEF;
}

a:visited {
    color: #871120;
}

pre {
    background-color: #f5deb3;
    color: black;
    padding: 20px;
}

.dark {
    background-color: #2C060B;
    background-color: rgba(10,0,0,0.8);
    padding: 20px;
    border: 1px solid black;
    border-radius: 20px;
}

.adminPanel {
    margin-top: 0px;
    border-radius: 0px 0px 20px 20px;
}

.supercontainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-top: -8px;
}

.row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.col {
    min-width: 800px;
    width:80%;
    
}

.header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 12px 0px 0px 0px;
}

.content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.content-side {
    margin-right: 20px;
    width: 500px;
}

.content-main {
    width: 100%;
    padding: 20px 50px;
}
  
.title img {
    width: 100%;
}

.sidebar {
    padding: 20px;
    margin-right: 20px;
}

.frontimage {
    width: 60%;
    display: block;
    margin: 10px auto 50px auto;
    border: 3px solid black;
    border-radius: 25px;
}

.frontquote {
    font-size: 1.5em;
    text-align: center;
}

.frontquote span {
    position: relative;
    left: 8pc;
}

.songlink:hover {
    font-weight: bold;
    cursor: pointer;
}

.selectedsong {
    font-weight: bold;
}

.react-icons {
    display: inline-block;
    text-align: center;
    border: 1px solid green;
    height: 100%;
}

.search {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-bottom: 10px;
}

.searchBasic {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: right;
        justify-content: right;
}

.searchAdvanced {
    margin-top:10px;
}

.searchAdvanced hr {
    margin-top: 15px;
}

.login {
    color: white;
    font-weight: bold;
    margin-bottom: 20px;
    -ms-flex-pack: center;
        justify-content: center;
}

.login div {
    padding: 20px 50px;
}

.login div input {
    margin-left: 15px;
}

.songInput textarea {
    display: block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color: #f5deb3;
    color: black;
    padding: 20px;
    width: 100%;
}

.inputTable {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.inputTableCol {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.inputTableColRight {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.inputTableRow {
    height: 1.5em;
    margin: 5px;
}

.inputTable input[type=text] {
    width: 100%
}

/*** Admin Panel */

.adminPanel {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 0px;
}

.ap-left, .ap-right {
    padding: 10px 50px;
}

.adminButton {
    float: left;
    margin: 0px 20px;
}

.adminButton:hover {
    font-weight: bold;
    cursor: pointer;

}


